








import Vue from "vue";
import {TweenMax, Power2, TimelineLite, TimelineMax} from "gsap"

export default Vue.extend({
  methods: {
    enter(body: any) {
      const timeLine = new TimelineMax({
        onComplete: function() {}
      });

      timeLine.set(body, {
        css: {
          position: "fixed",
          marginTop: 20 + "%",
          height: 5,
          width: 0 + "%",
          color: "rgba(22, 22, 125, 0)"
        }
      });

      timeLine.to(body, 0.4, {
        css: {
          position: "fixed",
          marginTop: 20 + "%",
          height: 5,
          width: 100 + "%",
          color: "rgba(22, 22, 125, 0)"
        }
      });

      timeLine.to(body, 0.4, {
        css: {
          position: "fixed",
          marginTop: 0 + "%",
          height: 100 + "%",
          width: "auto",
          color: "rgba(22, 22, 125, .1)"
        }
      });

      timeLine.to(body, 0.4, {
        css: {
          position: "fixed",
          marginTop: 0 + "%",
          height: 100 + "%",
          width: "auto",
          color: "rgba(22, 22, 125, .5)"
        }
      });

      timeLine.to(body, 0.8, {
        css: {
          position: "relative",
          marginTop: 0 + "%",
          height: "auto",
          width: "auto",
          color: "rgba(22, 22, 125, 1)"
        }
      });
    }
  }
});
